.num{
    width: 40px !important;
    height: 40px !important;
    border-radius: 50%;
    text-align: center;
    line-height: 40px !important;
    background-color: #00b5fe;
}
.comm{
    background-color: #272440;
    padding: 15px;
    border-radius: 25px;
}
@media screen and (min-width:768px) {
    .row{
        position: relative;
    }
    .fix{
        position: sticky;
        height: 90vh;
        background-color: #121258;
        padding-right: 0 !important;
        padding-top: 20px;
    }
    .tut{
        overflow-y:scroll ;
        position: static !important;
        height: 90vh;
    }
    body{
        width: 100%;
        height: 100vh;
        overflow: hidden;
    }
    .main{
        position: static !important;
        height:90vh;
        overflow: hidden;
    }  
    p{
        margin: auto;
    }
    .active{
        background-color: #040425 !important;
    }
}
.bolds{
    font-size: 1.2em;
    font-weight: 600;
    color: #00b5fe;
}